<!-- eslint-disable object-curly-spacing -->
<template>
  <div class="common-layout">
    <el-container class="app-container">
      <!-- Â∑¶‰æßÁ¨îËÆ∞ÂàóË°® -->
      <el-aside width="260px" class="sidebar">
        <div class="sidebar-header">
          <span>üìù Á¨îËÆ∞ÂàóË°®</span>
        </div>
        <el-scrollbar class="note-list-scrollbar">
          <div class="sidebar-action-card " v-for="(item, index) in articlelist" :key="index"
            @click="jumparticle(item.id)" @contextmenu.prevent="showContextMenu(item, $event)">
            <p class="title-ellipsis">{{ item.title }}</p>
            <p>{{ item.updatetime }}</p>
          </div>
          <div v-if="menuVisible" class="custom-context-menu" :style="menuStyle" @click.stop>
            <div class="menu-item" @click="handleEdit()">
              <span>‰øÆÊîπ</span>
            </div>
            <div class="menu-item" @click="handleDelete()">
              <span>Âà†Èô§</span>
            </div>
          </div>
        </el-scrollbar>
        <div class="sidebar-footer">
          <p>‰ºó‰∫∫‰πã‰∏äÔºåÂº∫ËÄÖ‰πãÂàó</p>
        </div>
      </el-aside>

      <!-- Âè≥‰æß‰∏ªÂÜÖÂÆπÂå∫ -->
      <el-main class="main-content">
        <div class="main-header">
          <span>Âø´ËÆ∞1.0.2ÊµãËØïÁâà</span>
          <span>
            <el-button :icon="Setting" circle @click="setting()"></el-button>
            <el-button :icon="Edit" circle @click="Eidtor()" />
            <el-button :icon="Discount" circle @click="message()"></el-button>
          </span>
        </div>
        <div class="welcome-center" v-if="mainshow">
          <div class="welcome-title">Ê¨¢ËøéÂõûÊù•</div>
        </div>
        <div style="flex:1;background-color:aliceblue" v-else>
          <router-view></router-view>
        </div>
      </el-main>

    </el-container>
    <el-dialog v-model="isshow" width="500" center>
      <template #title>
        ËØ∑ÈÖçÁΩÆ‰Ω†ÁöÑÊï∞ÊçÆÂ∫ì
      </template>
      <template #default>
        <el-form>
          <el-form-item label="Êï∞ÊçÆÂ∫ìÂêçÁß∞">
            <el-input v-model="dburl" type="password" placeholder="ËØ∑ËæìÂÖ•" show-password :disabled="iswrite"
              size="default" />
          </el-form-item>
          <el-form-item label="Áî®Êà∑Âêç">
            <el-input v-model="root" type="password" placeholder="ËØ∑ËæìÂÖ•" show-password :disabled="iswrite"
              size="default" />
          </el-form-item>
          <el-form-item label="Êï∞ÊçÆÂ∫ìÂØÜÁ†Å">
            <el-input v-model="password" type="password" placeholder="ËØ∑ËæìÂÖ•" show-password :disabled="iswrite"
              size="default" />
          </el-form-item>
        </el-form>
      </template>
      <template #footer>
        <el-button type="warning">ÈáçÂÜô</el-button>
        <el-button type="primary" @click="setdatabase()">Á°ÆÂÆö</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import { useRouter } from 'vue-router'
import htmlDocx from 'html-docx-js/dist/html-docx'
import { saveAs } from 'file-saver'
// eslint-disable-next-line no-unused-vars
import { Setting, Edit, Discount } from '@element-plus/icons-vue'
// --- Ë∑ØÁî±ÂíåÂü∫Á°ÄÁä∂ÊÄÅ ---
const router = useRouter()
const isshow = ref(false)
const iswrite = ref(false)
const mainshow = ref(true)

// --- Êï∞ÊçÆÂ∫ìÈÖçÁΩÆÁõ∏ÂÖ≥Áä∂ÊÄÅ ---
const root = ref('')
const password = ref('')
const dburl = ref('')

// --- ÊñáÁ´†ÂàóË°®ÂíåÂè≥ÈîÆËèúÂçïÁä∂ÊÄÅ ---
const articlelist = ref([])
const menuVisible = ref(false)
const menuStyle = ref({})
const currentItem = ref(null)
// eslint-disable-next-line camelcase
const article_id = ref(null)
// --- onMounted (ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÊâßË°å) ---
onMounted(() => {
  fetch('http://localhost:9527/api/status').then(res => {
    if (!res.ok) {
      throw new Error(`HTTP ÈîôËØØ! Áä∂ÊÄÅ: ${res.status}`)
    }
    return res.json()
  }).then(res => {
    console.log('Êú™ÈÖçÁΩÆÔºåÊòæÁ§∫ËÆæÁΩÆÁïåÈù¢', res)
    isshow.value = true // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
  }).catch(error => {
    console.log('Â∑≤ÈÖçÁΩÆÊàñÊé•Âè£ÂºÇÂ∏∏ÔºåÂ∞ùËØïËé∑ÂèñÈÖçÁΩÆ:', error)
    fetch('http://localhost:9527/getConfig')
      .then(res => res.json())
      .then(res => {
        console.log('Ëé∑ÂèñÈÖçÁΩÆÊàêÂäü', res)
        root.value = res.object.username
        password.value = res.object.password
        dburl.value = res.object.dbUrl
        iswrite.value = true
        // Ëé∑ÂèñÊñáÁ´†ÂàóË°®
        return fetch('http://localhost:9527/getallarticleNote')
      })
      .then(res => res.json())
      .then(res => {
        console.log('Ëé∑ÂèñÊñáÁ´†ÂàóË°®ÊàêÂäü', res)
        articlelist.value = res // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
      })
      .catch(getConfigError => {
        console.error('Ëé∑ÂèñÈÖçÁΩÆÊàñÊñáÁ´†ÂàóË°®Â§±Ë¥•:', getConfigError)
      })
  })
})

// --- ÊñπÊ≥ïÂÆö‰πâ ---

// ÂàáÊç¢ËÆæÁΩÆÊòæÁ§∫ (ÊÇ®ÁöÑ setting ÂáΩÊï∞)
// eslint-disable-next-line no-unused-vars
const setting = () => {
  isshow.value = !isshow.value
}

// ËÆæÁΩÆÊï∞ÊçÆÂ∫ì
// eslint-disable-next-line no-unused-vars
const setdatabase = () => {
  fetch('http://localhost:9527/api/setup', {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      username: root.value,
      password: password.value,
      dbUrl: dburl.value
    })
  }).then(res => res.json())
    .then(res => {
      ElMessage(res.message)
      if (res.success) {
        window.close()
      }
    })
}

// Ë∑≥ËΩ¨Âà∞ÁºñËæëÂô®
// eslint-disable-next-line no-unused-vars
function Eidtor () {
  router.push('/write')
}

// Ë∑≥ËΩ¨Âà∞ÊñáÁ´†ËØ¶ÊÉÖ
// eslint-disable-next-line no-unused-vars
const jumparticle = (id) => {
  if (menuVisible.value) { // Â¶ÇÊûúËèúÂçïÂºÄÁùÄÔºåÂçïÂáªÂè™ÂÖ≥Èó≠ËèúÂçï
    hideContextMenu()
    return
  }
  console.log(id)
  mainshow.value = false
  router.push({
    name: 'article',
    params: { id: id }
  })
  article_id.value = id
}

// ---- Âè≥ÈîÆËèúÂçïÊ†∏ÂøÉÊñπÊ≥ï (Â∑≤ÂÖ®ÈÉ®‰øÆÊ≠£) ----

// ÊòæÁ§∫Âè≥ÈîÆËèúÂçï
// eslint-disable-next-line no-unused-vars
const showContextMenu = (item, event) => {
  menuVisible.value = true // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
  currentItem.value = item // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
  menuStyle.value = { // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
    top: `${event.clientY}px`,
    left: `${event.clientX}px`
  }
}

// ÈöêËóèËèúÂçï
const hideContextMenu = () => {
  menuVisible.value = false // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
  currentItem.value = null // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
}

// Â§ÑÁêÜ"‰øÆÊîπ"ÁÇπÂáª
// eslint-disable-next-line no-unused-vars
const handleEdit = () => {
  if (currentItem.value) { // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
    console.log(`ÂáÜÂ§á‰øÆÊîπ ID: ${currentItem.value.id} ÁöÑÊñáÁ´†`)
    router.push({
      name: 'write',
      query: {
        id: currentItem.value.id,
        articleTitle: currentItem.value.title
      }
    })
  }
  hideContextMenu() // ‰øÆÊ≠£ÔºöÁõ¥Êé•Ë∞ÉÁî®ÂáΩÊï∞Ôºå‰∏çÈúÄË¶Å this
}

// Â§ÑÁêÜ"Âà†Èô§"ÁÇπÂáª
// eslint-disable-next-line no-unused-vars
const handleDelete = () => {
  if (currentItem.value) { // ‰øÆÊ≠£Ôºö‰ΩøÁî® .value
    if (confirm(`Á°ÆÂÆöË¶ÅÂà†Èô§ÊñáÁ´†„Ää${currentItem.value.title}„ÄãÂêóÔºü`)) {
      console.log(`ÂáÜÂ§áÂà†Èô§ ID: ${currentItem.value.id} ÁöÑÊñáÁ´†`)
      // ‰øÆÊ≠£ÔºöÊìç‰Ωú articlelist.value
      articlelist.value = articlelist.value.filter(article => article.id !== currentItem.value.id)
      fetch('http://localhost:9527/deleteid', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          id: currentItem.value.id
        })
      }).then(res => {
        return res.json()
      }).then(res => {
        ElMessage(res.message)
      })
    }
  }
  hideContextMenu() // ‰øÆÊ≠£ÔºöÁõ¥Êé•Ë∞ÉÁî®ÂáΩÊï∞Ôºå‰∏çÈúÄË¶Å this
}
// eslint-disable-next-line no-unused-vars
const message = () => {
  console.log('ÂØºÂá∫‰∏∫WordÊñáÊ°£')
  fetch('http://localhost:9527/getid', {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ id: article_id.value })
  })
    .then(res => res.json())
    .then(res => {
      if (res && res.code === 200) {
        const blob = htmlDocx.asBlob(res.object.context)
        saveAs(blob, `${res.object.title}.docx`)
      }
    })
}
</script>

