<!-- src/components/VantaBirds.vue -->
<template>
  <div ref="vantaRef" class="vanta-background-container"></div>
</template>

<script setup>
// 1. --- ÂºïÂÖ•ÊâÄÊúâÂøÖË¶ÅÁöÑÊ®°Âùó ---
import { ref, onMounted, onBeforeUnmount, watch, defineExpose } from 'vue'
import * as THREE from 'three'
// ÊòéÁ°ÆÂºïÂÖ• BIRDS ÊïàÊûúÊñá‰ª∂
import birds from 'vanta/dist/vanta.birds.min.js'

// 2. --- ÂÆö‰πâÁªÑ‰ª∂ÁöÑ Props (API) for BIRDS ---
// Ëøô‰∫õ props ÊòØÊ†πÊçÆ Vanta.js ÂÆòÁΩë BIRDS ÊïàÊûúÁöÑÊñáÊ°£Êù•ÂÆö‰πâÁöÑ
// eslint-disable-next-line no-undef
const props = defineProps({
  // --- Vanta ÈÄöÁî®ÊéßÂà∂ÈÄâÈ°π ---
  mouseControls: { type: Boolean, default: true },
  touchControls: { type: Boolean, default: true },
  gyroControls: { type: Boolean, default: false },
  minHeight: { type: Number, default: 200.00 },
  minWidth: { type: Number, default: 200.00 },
  scale: { type: Number, default: 1.00 },
  scaleMobile: { type: Number, default: 1.00 },

  // --- BIRDS ÊïàÊûú‰∏ìÂ±ûÈÄâÈ°π ---
  backgroundColor: { type: [Number, String], default: 0x7192f }, // Ê∑±Ëâ≤ËÉåÊôØ
  color1: { type: [Number, String], default: 0xff0000 }, // È∏üÁæ§È¢úËâ≤1 (Ê∏êÂèò)
  color2: { type: [Number, String], default: 0xd1ff }, // È∏üÁæ§È¢úËâ≤2 (Ê∏êÂèò)
  birdSize: { type: [Number, String], default: 1.5 }, // È∏üÁöÑÂ§ßÂ∞è
  wingSpan: { type: [Number, String], default: 20.0 }, // ÁøÖËÜÄÂ±ïÂºÄÂπÖÂ∫¶
  speedLimit: { type: [Number, String], default: 5.0 }, // È£ûË°åÈÄüÂ∫¶ÈôêÂà∂
  separation: { type: [Number, String], default: 20.0 }, // È∏ü‰πãÈó¥ÁöÑÊúÄÂ∞èË∑ùÁ¶ª
  alignment: { type: [Number, String], default: 20.0 }, // È∏üÁæ§ÈòüÂàóÂØπÈΩêÁöÑË∂ãÂäø
  cohesion: { type: [Number, String], default: 20.0 }, // È∏üÁæ§ËÅöÈõÜÁöÑË∂ãÂäø
  quantity: { type: [Number, String], default: 4.0 }, // È∏üÁöÑÊï∞Èáè
  backgroundAlpha: { type: [Number, String], default: 0.6 } // ËÉåÊôØ‰∏çÈÄèÊòéÂ∫¶
})

// 3. --- ÁªÑ‰ª∂ÂÜÖÈÉ®Áä∂ÊÄÅÂíåÂºïÁî® ---
const vantaRef = ref(null)
let vantaEffect = null
const createVantaOptions = () => ({
  THREE,
  el: vantaRef.value,
  ...props

})

onMounted(() => {
  if (vantaRef.value) {
    vantaEffect = birds(createVantaOptions())
  }
})

onBeforeUnmount(() => {
  // Âú®ÈîÄÊØÅ vantaEffect ‰πãÂâçÔºåÂÖàÊääÂÆÉËµãÂÄº‰∏∫ null
  // ËøôÊòØ‰∏Ä‰∏™ÂÖ≥ÈîÆÁöÑÊ≠•È™§ÔºåÂèØ‰ª•Èò≤Ê≠¢ÂêéÁª≠ÁöÑ watch Êàñ resize ÂõûË∞ÉÂÜçÊ¨°Êìç‰Ωú‰∏Ä‰∏™Âç≥Â∞ÜË¢´ÈîÄÊØÅÁöÑÂØπË±°
  if (vantaEffect) {
    const effectToDestroy = vantaEffect
    vantaEffect = null // ÂÖ≥ÈîÆÔºöÁ´ãÂç≥ÂàáÊñ≠ÂºïÁî®
    effectToDestroy.destroy()
  }
})

// 5. --- ÂìçÂ∫îÂºèÊõ¥Êñ∞ ---
watch(props, () => {
  if (vantaEffect && typeof vantaEffect.setOptions === 'function') {
    vantaEffect.setOptions(createVantaOptions())
  }
}, { deep: true })

// 6. --- Êö¥Èú≤Ë∞ÉËØïÊé•Âè£ ---
defineExpose({
  vantaEffect,
  refresh: () => {
    if (vantaEffect) vantaEffect.setOptions(createVantaOptions())
  }
})
</script>

<style scoped>
.vanta-background-container {
  width: 100%;
  height: 100%;
}
</style>
