<template>
  <div style="border: 1px solid #ccc;height:99%">
    <Toolbar style="border-bottom: 1px solid #ccc;height:5%" :editor="editorRef" :defaultConfig="toolbarConfig"
      :mode="mode" />
    <Editor style="height:90%;" v-model="valueHtml" :defaultConfig="editorConfig" :mode="mode"
      @onCreated="handleCreated" />
    <div class="footer">
      <el-button type="primary" @click="sendImages()">Â≠òÂÖ•</el-button>
      <el-button type="warning" @click="changeshow()">È¢ÑËßà</el-button>
      <el-button type="primary" @click="changeimageshow()">ÊµÅÁ®ãÂõæË°®</el-button>
    </div>
    <el-dialog v-model="show" title="Shipping address">
      <template #title>
        <h1>{{ title }}</h1>
      </template>
      <template #default>
        <div v-html="valueHtml"></div>
      </template>
    </el-dialog>
     <el-drawer v-model="drawershow" direction='ttb' size="800">
      <template #default>
        <vueflow-component></vueflow-component>
      </template>
     </el-drawer>
  </div>
</template>
<script>
import '@wangeditor/editor/dist/css/style.css' // ÂºïÂÖ• css

import { onBeforeUnmount, ref, shallowRef, watch } from 'vue'
import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
import { ElMessage } from 'element-plus'
import VueflowComponent from '@/components/VueflowComponent.vue'
import SparkMD5 from 'spark-md5'
export default {
  components: { Editor, Toolbar, 'vueflow-component': VueflowComponent },
  props: {
    title: String,
    articleId: Number
  },
  setup (props) {
    // ÁºñËæëÂô®ÂÆû‰æãÔºåÂøÖÈ°ªÁî® shallowRef
    const editorRef = shallowRef()
    const drawershow = ref(false)
    const changeshow = () => {
      show.value = true
    }
    const changeimageshow = () => {
      console.log('888')
      drawershow.value = true
    }
    // ÂÜÖÂÆπ HTML
    const valueHtml = ref('<p>hello</p>')
    const show = ref(false)
    const sendarticleId = ref()
    const localFilesToUpload = new Map()
    const toolbarConfig = {}
    const editorConfig = {
      placeholder: 'ËØ∑ËæìÂÖ•ÂÜÖÂÆπ...',
      MENU_CONF: {
        uploadImage: {
          customUpload: async (file, insertFn) => {
            const localId = await Md5(file)

            // 1. ÁîüÊàêÂîØ‰∏ÄÊ†áËØÜÁ¨¶ localIdÔºàËøôÈáå‰ΩøÁî® MD5Ôºâ
            // 2. Â∞ÜÂéüÂßã File ÂØπË±°Â≠òÂÇ®Ëµ∑Êù•ÔºåÁ≠âÂæÖÊúÄÁªà‰∏ä‰º†
            localFilesToUpload.set(localId, file)
            console.log('ÊöÇÂ≠òÊú¨Âú∞Êñá‰ª∂:', localId, file.name)

            // 3. ‰ΩøÁî® FileReader Â∞ÜÂõæÁâáËΩ¨‰∏∫ Base64
            const reader = new FileReader()
            reader.onload = (e) => {
              const base64Url = e.target.result
              const editor = editorRef.value
              if (editor) {
                const imgHtml = `<img src="${base64Url}" alt="${file.name}" data-local-id="${localId}" style="max-width: 100%;"/>`
                editor.dangerouslyInsertHtml(imgHtml)
              }
            }
            reader.readAsDataURL(file)
          }
        }
      }
    }
    watch(
      // 1. ÁõëÂê¨Ê∫êÔºö‰∏Ä‰∏™ËøîÂõû props.articleId ÁöÑÂáΩÊï∞
      () => props.articleId,

      // 2. ÂõûË∞ÉÂáΩÊï∞ÔºöÂΩì articleId ÂèòÂåñÊó∂ÊâßË°å
      (newId) => {
        // newId Â∞±ÊòØ props.articleId ÁöÑÊúÄÊñ∞ÂÄº
        console.log(newId)
        // 3. Â∞ÜÊÇ®ÁöÑ if/else ÈÄªËæëÊê¨Âà∞ËøôÈáå
        if (newId === null || newId === undefined) {
          // Â¶ÇÊûúÊñ∞ÁöÑ ID ÊòØ nullÔºåËØ¥ÊòéÊòØÊñ∞Â¢ûÊ®°ÂºèÊàñÈúÄË¶ÅÈáçÁΩÆ
          console.log('watch: articleId ‰∏∫ nullÔºåËøõÂÖ•Êñ∞Â¢ûÊ®°ÂºèÊàñÈáçÁΩÆÁä∂ÊÄÅ„ÄÇ')
          valueHtml.value = '' // Ê∏ÖÁ©∫ÁºñËæëÂô®ÂÜÖÂÆπ
        } else {
          // Â¶ÇÊûúÊñ∞ÁöÑ ID ÊòØ‰∏Ä‰∏™ÊúâÊïàÁöÑÊï∞Â≠ó
          console.log(`watch: articleId Âèò‰∏∫ ${newId}ÔºåÂºÄÂßãËé∑ÂèñÊñáÁ´†Êï∞ÊçÆ...`)
          sendarticleId.value = newId
          console.log(sendarticleId.value)
          fetch('http://localhost:9527/getid', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ id: newId }) // ‰ΩøÁî® newId
          })
            .then(res => res.json())
            .then(res => {
              if (res && res.code === 200) {
                console.log('watch: Êï∞ÊçÆËé∑ÂèñÊàêÂäü', res)
                // ËØ∑Á°ÆËÆ§ÂêéÁ´ØËøîÂõûÁöÑÂ≠óÊÆµÊòØ context ËøòÊòØ content
                valueHtml.value = res.object.content || res.object.context || ''
              } else {
                ElMessage('Ëé∑ÂèñÊñáÁ´†Êï∞ÊçÆÂ§±Ë¥•ÔºåËØ∑Âà∑Êñ∞ÈáçËØï')
              }
            })
        }
      },

      // 4. ÈÖçÁΩÆÂØπË±°
      {
        // immediate: true Á°Æ‰øù watch Âú®ÁªÑ‰ª∂ÂàõÂª∫Êó∂Â∞±Á´ãÂç≥ÊâßË°å‰∏ÄÊ¨°
        // ËøôÂØπ‰∫éÂ§ÑÁêÜÂàùÂßãÁä∂ÊÄÅËá≥ÂÖ≥ÈáçË¶Å
        immediate: true
      }
    )
    onBeforeUnmount(() => {
      const editor = editorRef.value
      if (editor == null) return
      editor.destroy()
    })

    const handleCreated = (editor) => {
      editorRef.value = editor // ËÆ∞ÂΩï editor ÂÆû‰æãÔºåÈáçË¶ÅÔºÅ
    }
    const sendImages = () => {
      if (localFilesToUpload.size === 0) {
        sendarticle()
        return
      }
      const formData = new FormData()

      // 2. ÈÅçÂéÜ MapÔºåÂ∞Ü file Âíå localId ÂàÜÂà´Ê∑ªÂä†Âà∞ FormData ‰∏≠
      localFilesToUpload.forEach((file, localId) => {
        console.log('ÂáÜÂ§á‰∏ä‰º†Êñá‰ª∂:', localId, file.name)
        formData.append('files', file)
        formData.append('localIds', localId)
      })
      fetch('http://localhost:9527/upload_artile/artile_img', {
        method: 'POST',
        body: formData
      }).then(res => {
        return res.json()
      }).then(res => {
        const uploadedResults = res.object

        // 4. ËøõË°å‰∏ÄÂØπ‰∏ÄÊõøÊç¢
        let tempHtml = valueHtml.value
        uploadedResults.forEach(({ localId, realUrl }) => {
          const imgRegex = new RegExp(`src="${localId}"`, 'g')
          tempHtml = tempHtml.replace(imgRegex, `src="${realUrl}"`)
        })
        valueHtml.value = tempHtml
        sendarticle()
      })
    }

    const sendarticle = () => {
      console.log(sendarticleId.value)
      if (!sendarticleId.value) {
        fetch('http://localhost:9527/insertarticle', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({

            title: props.title,
            context: valueHtml.value
          })
        }).then(res => {
          return res.json()
        }).then(res => {
          ElMessage(res.message)
        })
      } else {
        fetch('http://localhost:9527/updatearticle', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            id: sendarticleId.value,
            title: props.title,
            context: valueHtml.value
          })
        }).then(res => {
          return res.json()
        }).then(res => {
          ElMessage(res.message)
        })
      }
    }
    const Md5 = (file) => {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()

        // 1. ÂÖàÂëäËØâ reader ËØªÂèñÂÆå‰πãÂêéË¶ÅÂÅö‰ªÄ‰πà
        reader.onload = (e) => {
          // ËøôÈáåÁöÑ e ÊòØ‰∫ã‰ª∂ÂØπË±°Ôºåe.target.result Â∞±ÊòØËØªÂà∞ÁöÑÊñá‰ª∂‰∫åËøõÂà∂Êï∞ÊçÆ
          const spark = new SparkMD5.ArrayBuffer()
          spark.append(e.target.result)
          const md5 = spark.end()
          resolve(md5) // ÊàêÂäüÂêéÔºåÊääÁªìÊûú‚ÄúÂèëË¥ß‚ÄùÂá∫Âéª
        }

        reader.onerror = (e) => {
          reject(e) // Â§±Ë¥•‰∫ÜÔºåÊääÈîôËØØ‚ÄúÂèëË¥ß‚ÄùÂá∫Âéª
        }

        // 3. ÊúÄÂêéÂºÄÂßãÊ≠£ÂºèËØªÂèñ
        reader.readAsArrayBuffer(file)
      })
    }
    return {
      editorRef,
      valueHtml,
      mode: 'default', // Êàñ 'simple'
      toolbarConfig,
      editorConfig,
      handleCreated,
      changeshow,
      show,
      sendarticle,
      sendImages,
      drawershow,
      changeimageshow
    }
  }
}
</script>
<style scoped>
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1.5px solid rgb(204, 204, 204);
  padding: 10px 0 10px 0;
  height: 3%;
}
</style>
