<template>
  <!-- 1. ÂáÜÂ§á‰∏Ä‰∏™ÂÆπÂô® divÔºåReact ÁªÑ‰ª∂Â∞ÜË¢´Ê∏≤ÊüìÂà∞ËøôÈáå -->
  <div ref="containerRef" class="excalidraw-wrapper"></div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, watch } from 'vue'

// 2. ÂºïÂÖ• React Âíå ReactDOM
import React from 'react'
import ReactDOM from 'react-dom'

// 3. ÂºïÂÖ• Excalidraw ÁªÑ‰ª∂ÂíåÂÆÉÁöÑÁ±ªÂûãÔºàÂ¶ÇÊûú‰ΩøÁî® TypeScriptÔºâ
import { Excalidraw } from '@excalidraw/excalidraw'
import '@excalidraw/excalidraw/index.css'

// 4. ÂÆö‰πâ props Âíå emitsÔºåÁî®‰∫é‰∏éÁà∂ Vue ÁªÑ‰ª∂ÈÄö‰ø°
// eslint-disable-next-line no-undef
const props = defineProps({
  // Áî®‰∫éÂä†ËΩΩÂàùÂßãÊï∞ÊçÆ
  initialData: {
    type: Object,
    default: null
  },
  // Áî®‰∫éÊéßÂà∂Âè™ËØªÊ®°Âºè
  viewMode: {
    type: Boolean,
    default: false
  }
})

// eslint-disable-next-line no-undef
const emit = defineEmits(['change']) // ÂΩìÁôΩÊùøÂÜÖÂÆπÊîπÂèòÊó∂ÔºåÂêëÂ§ñËß¶Âèë‰∫ã‰ª∂

// 5. ÂàõÂª∫‰∏Ä‰∏™ ref Êù•ÂºïÁî®ÂÆπÂô® div
const containerRef = ref(null)

// ÂÆö‰πâ‰∏Ä‰∏™ÂáΩÊï∞Êù•Ê∏≤Êüì/Êõ¥Êñ∞ React ÁªÑ‰ª∂
const renderExcalidraw = () => {
  if (containerRef.value) {
    // ÂáÜÂ§áË¶Å‰º†ÈÄíÁªô Excalidraw ÁöÑ props
    const excalidrawProps = {
      initialData: props.initialData,
      viewModeEnabled: props.viewMode,
      langCode: 'zh-CN', // ËÆæÁΩÆËØ≠Ë®Ä‰∏∫‰∏≠Êñá
      onChange: (elements, appState, files) => {
        // ÂΩì React ÁªÑ‰ª∂ÂÜÖÈÉ®ÂÜÖÂÆπÂèòÂåñÊó∂ÔºåÈÄöËøá emit Â∞ÜÊï∞ÊçÆ‰º†ÂõûÁªô Vue Áà∂ÁªÑ‰ª∂
        emit('change', { elements, appState, files })
      }
      // ... ‰Ω†ÂèØ‰ª•Âú®ËøôÈáå‰º†ÈÄí‰ªª‰Ωï Excalidraw ÊîØÊåÅÁöÑ prop
    }

    // ‰ΩøÁî® React.createElement ÂàõÂª∫ÁªÑ‰ª∂ÂÆû‰æãÔºåÂπ∂Áî® ReactDOM.render Ê∏≤ÊüìÂÆÉ
    ReactDOM.render(React.createElement(Excalidraw, excalidrawProps), containerRef.value)
  }
}

// 6. Âú®ÁªÑ‰ª∂ÊåÇËΩΩÂêéÔºåÈ¶ñÊ¨°Ê∏≤Êüì React ÁªÑ‰ª∂
onMounted(() => {
  renderExcalidraw()
})

// 7. Âú®ÁªÑ‰ª∂Âç∏ËΩΩÂâçÔºåÊ∏ÖÁêÜ React ÁªÑ‰ª∂ÔºåÈò≤Ê≠¢ÂÜÖÂ≠òÊ≥ÑÊºè
onUnmounted(() => {
  if (containerRef.value) {
    ReactDOM.unmountComponentAtNode(containerRef.value)
  }
})

// 8. (ÈáçË¶Å) ÁõëÂê¨ props ÁöÑÂèòÂåñÔºåÂΩìÁà∂ÁªÑ‰ª∂‰º†ÈÄíÁöÑÊï∞ÊçÆÊõ¥Êñ∞Êó∂ÔºåÈáçÊñ∞Ê∏≤Êüì React ÁªÑ‰ª∂
watch(() => props.initialData, () => {
  console.log('Initial data changed, re-rendering Excalidraw...')
  renderExcalidraw()
})
watch(() => props.viewMode, () => {
  console.log('View mode changed, re-rendering Excalidraw...')
  renderExcalidraw()
})
</script>

<style scoped>
/* Á°Æ‰øùÂåÖË£ÖÂô®ÂíåÂÆÉÁöÑÂÆπÂô®ÊúâÊòéÁ°ÆÁöÑÈ´òÂ∫¶ÔºåÂê¶Âàô Excalidraw Â∞ÜÊó†Ê≥ïÊòæÁ§∫ */
.excalidraw-wrapper {
  width: 100%;
  height: 100%; /* ÊàñËÄÖ‰∏Ä‰∏™ÂÖ∑‰ΩìÁöÑÂÉèÁ¥†ÂÄºÔºåÂ¶Ç 800px */
}
</style>
